/**
 *
 * @title 
 * @description size
 *
 */

import React, { Component } from 'react';
import Button from 'bee-button';
import Modal from '../../src';

class Demo3 extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            modalSize: ''
        };
        this.close = this.close.bind(this);
        this.open = this.open.bind(this);
        this.changeSize = this.changeSize.bind(this);

    }

    close() {
        this.setState({
            showModal: false
        });
    }

    open() {
        this.setState({
            showModal: true
        });
    }

    changeSize(size) {
        this.setState({
            modalSize: size
        });
    }

    render() {
        return (
            <div>
                <Button
                    colors="primary"
                    size="lg"
                    className="demo-margin"
                    onClick={ ()=>{this.changeSize("sm");this.open();} }>
                    
                </Button>
                <Button
                    colors="primary"
                    size="lg"
                    className="demo-margin"
                    onClick={ ()=>{this.changeSize("lg");this.open();} }>
                    
                </Button>
                <Button
                    colors="primary"
                    size="lg"
                    className="demo-margin"
                    onClick={ ()=>{this.changeSize("xlg");this.open();} }>
                    
                </Button>
                <Modal show={ this.state.showModal } size={ this.state.modalSize } onHide={ this.close }>
                    <Modal.Header closeButton>
                        <Modal.Title >  </Modal.Title>
                    </Modal.Header >
                    <Modal.Body >
                        <h4>  </h4>

                        <p>  </p>
                        <hr/>
                        <h4>   </h4>

                        <p>  </p>

                        <p>     
                        </p>

                        <p>    </p>

                        <p>    </p>

                        <p>      </p>

                        <p>     </p>

                        <p>  </p>

                        <p>   </p>

                        <p>   </p>

                        <p>    </p>

                        <p>          </p>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={ this.close }>  </Button>
                    </Modal.Footer>
                </Modal>
            </div>
        )
    }
}

export default Demo3;



